export const fetchRepos = async (org) => {
    const response = await fetch(`https://api.github.com/orgs/${org}/repos`, {
        method: 'GET',
        cache: 'no-cache',
        credentials: 'omit',
        headers: {
            'X-GitHub-Media-Type' : 'github.v3; param=full; format=json',
        }
    });
    if (!response.ok) {
        new Error((response).statusText);
    }
    return await response;
};